/* ===== Header Styling ===== */
.cardHeader {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0;
    height: 10%;
    border-radius: 12px 12px 0 0;
}

.leftMidWrapper {
    display: flex;
    align-items: center;
    height: 100%;
}

.leftSection {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    background-color: var(--header-color);
    color: var(--secondary-color);
    border-radius: 12px 0 0 0;
    font-weight: bold;
    height: 100%;
}

.leftSection2 {
    background-color: var(--header-color);
    color: var(--secondary-color);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    font-weight: bold;
    font-size: 18px;
    margin-right: 1.5rem;
    border-left: 3px solid var(--secondary-color);
    height: 100%;
}

.midSection {
    display: flex;
    gap: 1rem;
    align-items: flex-end;
}

.backButton {
    background: none;
    border: none;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.backButton:hover {
    color: var(--secondary-color-hover);
}

.inputGroup {
    display: flex;
    flex-direction: column;
    align-items: start;
    font-size: 12px;
    font-weight: bold;
    width: 15dvw;
}

.inputGroup input[type="date"] {
    padding: 6px 12px;
    border: 0px solid black;
    border-radius: 5px;
    font-size: 14px;
    background-color: white;
    color: var(--header-color-hover);
}

.switchGroup {
    display: flex;
    flex-direction: column;
    align-items: start;
    font-size: 14px;
    font-weight: bold;
    width: 15dvw;
}

.switch {
    position: relative;
    display: inline-block;
    width: 68px;
    height: 34px;
    margin-right: 10px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    background-color: #ccc;
    border-radius: 34px;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: 0.4s;
    border-radius: 50%;
}

.switch input:checked+.slider {
    background-color: var(--header-color);
}

.switch input:checked+.slider:before {
    transform: translateX(33px);
}

.buttonFooter {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0.7rem 0.9rem;
    border-radius: 12px;
    background-color: var(--header-color);
    color: #ffffff;
    font-size: 1.8rem;
    cursor: pointer;
    transition: all 0.3s ease;
    border: none;
}

.buttonFooter:hover {
    background-color: var(--header-color-hover);
    color: #ffffff;
    transform: translateY(-2px);
}

.buttonFooter:disabled {
    background-color: #cccccc;
    color: #666666;
    cursor: not-allowed;
    opacity: 0.7;
    transform: none;
    pointer-events: none;
}

.buttonFooter:disabled:hover {
    background-color: #cccccc;
    color: #666666;
}

.rightSection {
    background-color: var(--header-color);
    color: var(--secondary-color);
    border-radius: 0 12px 0 0;
    font-weight: bold;
    letter-spacing: 1px;
    display: flex;
    gap: 1rem;
}

.rightSection>div {
    font-size: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
}

.rightSection>button {
    border: none;
    background-color: var(--header-color);
    color: var(--secondary-color);
    font-size: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.rightSection>button:hover {
    background-color: var(--header-color-hover);
}

.rightSection>button:last-child {
    border-top-right-radius: 12px;
}

button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* ===== End Header Styling ===== */





/* ===== Body Styling ===== */
/* CARD */
.cardContainer {
    width: 100%;
    flex: 1;
    overflow: hidden;
}

.cardWrapper {
    border-radius: 10px;
    box-sizing: border-box;
    overflow-y: auto;
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: 75dvh;
    gap: 1rem;
    padding: 1rem;
}




.cardParent {
    background: var(--card-color-hover);
    color: var(--secondary-color);
    border-radius: 16px;
    font-weight: 500;
    box-shadow: -3px 3px 6px rgba(0, 0, 0, 0.65);
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.cardParentHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--header-color);
    color: var(--secondary-color);
    border-radius: 16px 16px 0 0;
    width: 100%;
    font-size: 1.6rem;
    flex-direction: column;
    height: 28%;
    overflow: hidden;
    box-shadow: -1px 1px 4px rgba(0, 0, 0, 0.7);
}

.titleHeader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: var(--header-color);
    color: var(--secondary-color);
    font-size: 1.5rem;
    font-weight: bold;
    border-bottom: 3px solid var(--secondary-color);
    padding: 0.1rem 0;
}

.bodyHeader {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0.5rem 1rem;
    overflow: hidden;
}

.bodyHeaderLeft {
    width: 60%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.itemHeaderLeft {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.itemHeaderLeft>div:first-child {
    font-size: 14px;
    color: white;
}

.oolNumber {
    background-color: var(--header-color);
    color: white;
    font-size: 14px;
    font-weight: bold;
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.25);
    padding: 0.1rem;
    width: 25%;
}

.cenNumberNormal {
    background-color: var(--connect-color);
}

.cenNumberWarning {
    background-color: var(--warning-color);
}

.cenNumberAlarm {
    background-color: var(--disconnect-color);
}

.bodyHeaderLeft>div:first-child .oolNumber {
    border-radius: 8px 8px 0 0;
}

.bodyHeaderLeft>div:last-child .oolNumber {
    border-radius: 0 0 8px 8px;
}


.bodyHeaderRight {
    width: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    overflow: hidden;
    padding: 0.25rem;
}

.imgWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--header-color);
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.65);
    height: 100%;
    width: auto;
    border-radius: 8px;
    overflow: hidden;
}

.imgHeader {
    height: 100%;
    width: auto;
}




.cardParentBodyWrapper {
    display: flex;
    flex: 1;
    min-height: 0;
    overflow: hidden;
    flex-direction: column;
}

.cardParentBody {
    overflow-y: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: calc((100% / 2) - 0.5rem);
    gap: 1rem;
    padding: 1rem;
    height: 100%;
    width: 100%;
}

.gridItem {
    border-radius: 12px;
    overflow: hidden;
    color: white;
    display: flex;
    flex-direction: column;
    box-shadow: -4px 4px 8px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
    cursor: pointer;
    gap: 0.25rem;
    background-color: var(--header-color);
}

.gridItem:hover {
    transform: translateY(-5px);
    box-shadow: -8px 8px 16px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.gridTitle {
    display: flex;
    align-items: start;
    justify-content: start;
}

.gridTitleText {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--secondary-color);
    color: var(--header-color);
    padding: 0.25rem 1.5rem;
    font-size: 18px;
    font-weight: bold;
    border-radius: 12px 0;
}

.gridContent {
    flex: 1;
    padding: 0.5rem;
    gap: 0.5rem;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: calc((100% - 0.5rem) / 2);
}

.gridContentText {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: var(--header-color);
    border-radius: 8px;
    box-shadow: -2px 2px 4px rgb(0, 0, 0, 0.5);
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.gridContentText>div:first-child {
    font-size: 12px;
}

.gridContentText:hover {
    transform: translateY(-3px);
    box-shadow: -4px 4px 8px rgb(0, 0, 0, 0.5);
}

.gridContentText>div:last-child {
    color: var(--secondary-color);
    font-weight: bold;
    letter-spacing: 0.02em;
    font-size: 14px;
}

.gridFooter {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    gap: 1rem;
    background-color: var(--secondary-color);
}

.gridFooterItem {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0.5rem 0;
}

.gridFooterItem p {
    margin: 0;
    font-weight: bold;
    color: var(--header-color);
}

.footerIndicator {
    width: 22px;
    height: 22px;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.3);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.connected {
    background-color: var(--connect-color);
}

.disconnected {
    background-color: var(--disconnect-color);
}



/* Centerline */
.gridItemCenter {
    background-color: var(--header-color);
    border-radius: 12px;
    overflow: hidden;
    color: white;
    display: flex;
    flex-direction: column;
    box-shadow: -4px 4px 8px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
}

.gridItemCenter:hover {
    transform: translateY(-5px);
    box-shadow: -8px 8px 16px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.gridCenterHeader {
    display: flex;
    width: 100%;
    border-bottom: 3px solid var(--secondary-color);
    padding: 0.25rem 0;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.gridCenterHeader h3 {
    font-size: 1.25rem;
}

.gridCenterHeader p {
    font-size: 0.75rem;
}

.gridCenterContent {
    width: 100%;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
}


.warningAlarm {
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-30%);
    display: flex;
    flex-direction: column;
    font-size: 0.75rem;
    gap: 0.25rem;
}

.warningAlarm div {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    color: white;
    font-weight: bold;
    padding: 0.25rem;
}

.alarmColorBox {
    background-color: var(--disconnect-color);
}

.warnColorBox {
    background-color: var(--warning-color);
}


.booleanContent {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80%;
    width: 80%;
    background-color: var(--connect-color);
    border-radius: 8px;
    box-shadow: -2px 2px 4px rgba(0, 0, 0, 0.7);
}

.booleanLeft {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 50%;
    font-size: 0.75rem;
}

.booleanRight {
    display: flex;
    align-items: end;
    justify-content: end;
    flex-direction: column;
    height: 100%;
    width: 50%;
    padding: 1rem;
}

.booleanStatus {
    display: flex;
    align-items: center;
    justify-content: center;
    height: auto;
    width: 100%;
    box-shadow:
        3px 3px 6px rgba(20, 17, 17, 0.7),
        -3px -3px 6px rgba(255, 217, 217, 0.3);
    border-radius: 8px;
    padding: 0.5rem;
}

.notOk {
    background-color: var(--disconnect-color);
}

