/* Following Error */

.gridContentFoll {
    flex: 1;
    padding: 1.5rem;
    gap: 0.5rem;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
}

.gridContentTextFoll {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 75%;
    background-color: var(--header-color);
    border-radius: 8px;
    box-shadow: -3px 3px 6px rgb(0, 0, 0, 0.5);
    transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
}

.gridContentTextFoll>div:first-child {
    font-size: 12px;
}

.gridContentTextFoll:hover {
    transform: translateY(-3px);
    box-shadow: -4px 4px 8px rgb(0, 0, 0, 0.5);
}

.gridContentTextFoll>div:last-child {
    color: var(--secondary-color);
    font-weight: bold;
    letter-spacing: 0.02em;
    font-size: 14px;
}



@keyframes blinkWarning {
    0% {
        background-color: var(--warning-color);
    }

    55% {
        background-color: var(--header-color);
    }

    100% {
        background-color: var(--warning-color);
    }
}

.warningBackground {
    animation:
        blinkWarning 2s infinite alternate ease-in-out;
}

@keyframes blinkDanger {
    0% {
        background-color: var(--disconnect-color);
    }

    55% {
        background-color: var(--header-color);
    }

    100% {
        background-color: var(--disconnect-color);
    }
}

.dangerBackground {
    animation:
        blinkDanger 2s infinite alternate ease-in-out;
}

.noData {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: var(--header-color);
    font-weight: bold;
}

/* ===== End Body Styling ===== */